<template>
    <div class="content-breadcrumb">
    <el-breadcrumb separator="/">
        <el-breadcrumb-item :key="indexItem" v-for="(index, indexItem) in breadcrumb" :to="{ path: index.url }">
            {{ index.title }}
        </el-breadcrumb-item>
    </el-breadcrumb>
    </div>
</template>

<script>
export default {
    computed : {
        breadcrumb () { 
            return this.$route.meta.breadcrumb
        }
    }
}
</script>

<style>
.content-breadcrumb {
    margin: 0;
    padding: 25px;
}
</style>