<template>
    <div>
        <div class="sidebar">
            <the-navbar-vertical />
        </div>
        <!-- <div class="navbar" v-else>
            <the-navbar-horizontal />
        </div> -->
        <div :class="contentClass">
            <breadcrumb v-if="breadcrumb !== 'Dashboard'" />
            <router-view />
        </div>
    </div>
</template>

<script>

import TheNavbarVertical from '@/layouts/component/NavBarVertical.vue'
// import TheNavbarHorizontal from '@/layouts/component/NavBarHorizontal.vue'
import Breadcrumb from '@/layouts/component/Breadcrumb.vue'

export default {
    data () {
        return {
            widthResize : '250px',
            // contentClass : 'content'
        }
    },
    components : {
        TheNavbarVertical,
        // TheNavbarHorizontal,
        Breadcrumb,
    },
    computed : {
        breadcrumb () { 
            return this.$route.name
        },
        windowWidth () { 
            return this.$store.state.windowWidth
        },
        contentClass () {
            if (this.$store.state.windowWidth >= 1000) {
                var classCss = 'content'
            } else {
                classCss = 'content-min'
            }
            return classCss
        }
    },
}
</script>

<style>
body {
    background-color: #F2F6FC;
    font-family: sans-serif;
	font-style: normal;
}
.sidebar {
  width: 260px;
  height: 100%;
  position: fixed;
  height: 100%;
  overflow: auto;
}
div.content {
  margin-left: 260px;
  height: 100%;

}
div.content-min {
  margin-top: 60px;
  height: 100%;
}
</style>
