<template>
    <div>
      <vs-sidebar
        v-model="active"
        :open="open"
        >
        <template #logo>
          <img src="@/assets/logo.png"/>
        </template>
        <vs-sidebar-item :key="indexItem" v-for="(index, indexItem) in menu" :id="index.name" :to="baseUrl + index.url">
          <template #icon>
            <i :class="index.icon"></i>
          </template>
          {{ index.name }}
        </vs-sidebar-item>


        <template #footer>
          <vs-row justify="space-between">
            <vs-avatar>
              <el-image :src="photoUser" fit="cover"/>
            </vs-avatar>
            <el-button type="danger" icon="el-icon-arrow-right" circle @click="logOut()"></el-button>
            </vs-row>
        </template>
      </vs-sidebar>
    </div>
</template>



<script>
export default {
  data() {
    return {
       active : '',
       open : true
    }
  },
  methods : {
    logOut () {
      // sessionStorage.clear()
      localStorage.clear()
      if (!localStorage.getItem('userData')) {
        window.location.replace(this.$router.currentRoute.query.to || '/login')
      }
      // this.$router.push('/login')
    },
    
  },
  computed : {
    baseUrl () {
      const path = this.$route.path
      return path[0]
    },
    activeMenu () {
      return this.$route.meta.active
    },
    menu () {
      return this.$store.state.menu
    },
    photoUser () {
      var photoUser = this.$store.state.userDataActive.photo
      if (!photoUser) {
        return this.$store.state.userDataActive.photo
      } else {
        return this.$store.state.userDataActive.photo
      }
    }
  },
  created () {
    this.active = this.activeMenu
  }
}
</script>

